<template>
    <div id="goodsPage" class="goodsDetail">
        <!--顶部吸顶-->
        <!--<van-skeleton title :row="3" />-->
        <div v-show="show.normalTitle" class="title" :style="setTopHeight">
            <img class="arrow" :src='icon.arrowBg' @click="onClickLeft">
            <!-- <img class="cart" :src="icon.shoppingCartBg">-->
            <van-icon class="cart" :name="icon.shoppingCartBg" :info="skuNum!=0?(skuNum<=99?skuNum+'':'99+'):''"
                      @click="turnToCart"></van-icon>
            <img v-show="show.beforeCollect" class=collection :src="icon.collectionBg" @click="addCollection">
            <img v-show="show.afterCollect" class=collection :src="icon.afterCollectionBg" @click="addCollection">
        </div>
        <div v-show="show.activeTitle" class="actTitle" :style="setTopHeight">
            <img class="arrow" :src='icon.arrow' @click="onClickLeft">
            <!--<img class="cart" :src="icon.shoppingCart">-->
            <van-icon class="cart" :name="icon.shoppingCart" :info="skuNum!=0?(skuNum<=99?skuNum+'':'99+'):''"
                      @click="turnToCart"></van-icon>
            <img v-show="show.beforeCollect" class=afterCollection :src="icon.collection" @click="addCollection">
            <img v-show="show.afterCollect" class=afterCollection :src="icon.afterCollection" @click="addCollection">
            <div class="tabChange" v-if="lists.detailDescribe" :style="setTopHeight">
                <div class="tabTxtList">
                    <div :class="{active:show.titShow==0}" class="evaluateBtn" @click="goodsChange">商品</div>
                    <div :class="{active:show.titShow==1}" class="evaluateBtn" @click="titChange">评价</div>
                    <div :class="{active:show.titShow==2}" class="detailBtn" @click="detailChange">详情</div>
                </div>
                <div :style="{'padding':'0px 3px'}" style='position: relative;'>
                    <img :src="icon.topClick" class="leftImgBox" :class="show.titShow==1?'topClick':show.titShow==2?'choicePoint':''">
                </div>
            </div>

            <div class="noDetailTabChange" v-if="!lists.detailDescribe" :style="setTopHeight">
                <div class="tabTxtList">
                    <div :class="{active:show.titShow==0}" class="evaluateBtn" @click="goodsChange">商品</div>
                    <div :class="{active:show.titShow==1}" class="evaluateBtn" @click="titChange">评价</div>
                </div>
                <div :style="{'padding':'0px 3px'}" style='position: relative;'>
                    <img :src="icon.topClick"  class="leftImgBox1">
                    <!-- <img :src="icon.topClick" class="choicePoint" v-if="show.titShow==2"> -->
                </div>
            </div>
        </div>
        <div ref="goodsPageScroll" @scroll="handleScroll" class="mianScroll" >
            <!--商品轮播图-->
            <van-swipe class="swipe" :loop="true"  @change="bannerChange" :autoplay="3000">
                <van-swipe-item v-for="(image,i) in productImgs">
                    <img :src="image" @click="bannerPreview(productImgs,i)"/>
                    <!-- 根据接口判断秒杀状态，在轮播图上显示圆形蒙版-->
                    <div class="outSellBox" v-if="status==0">即将开始</div>
                    <div class="outSellBox" v-if="status==3||status==4">补货中</div>
                </van-swipe-item>
                <!-- <div class="van-swipe__indicators" v-if="productImgs.length==1&&show.networkError==false">
                  <i class="van-swipe__indicator van-swipe__indicator--active"></i>
                </div> -->
            </van-swipe>
            <van-skeleton title :row="6" v-if="show.networkError" :style="{'margin-top':'20px'}"/>
            <div v-if="show.networkError==false">
                <!-- 即将开始的秒杀和秒杀中（包括补货中） -->
                <div
                        class="price"
                        v-if="status!=2&&status!=-1"
                        :style="{'background':(status==0?'#F5A623':(status==1?'#17B7BD':'#999999'))}"
                >
                    <div class="priceLeftBox">
                        <div class="priceBox">
                            <div class="priceTag">￥</div>
                            <div class="salePrice">{{flashSaleData.spuSkPrice}}</div>
                        </div>
                        <div class="originalPrice" v-if="flashSaleData.originalPrice !=null">￥{{flashSaleData.originalPrice}}</div>
                    </div>
                    <div class="priceRightBox">
                        <div class="repertoryBox">
                            <div>秒杀库存{{flashSaleData.skStoreBal}}件</div>
                            <div :style="{'color':(status==0?'#F5A623':(status==1?'#17B7BD':'#646464'))}">限购{{flashSaleData.limitAccount}}件</div>
                        </div>
                        <div class="timeRightBox">
                            <div class="seckillTxt" v-if="status==0" style="color: #F5A623">距秒杀开始还有</div>
                            <div class="seckillTxt" v-if="status==1||status==3||status==4">距秒杀结束仅剩</div>
                            <van-count-down :time="flashSaleData.time" @finish="countDownFinish">
                                <template v-slot="timeData" class="seckillDate">
          <span
                  class="item"
                  :style="{'background':(status==0?'#F5A623':(status==1?'#17B7BD':'#646464'))}"
          >{{ timeData.hours}}</span>
                                    <span :style="{'color':(status==0?'#F5A623':(status==1?'#17B7BD':'#646464'))}">:</span>
                                    <span
                                            class="item"
                                            :style="{'background':(status==0?'#F5A623':(status==1?'#17B7BD':'#646464'))}"
                                    >{{ timeData.minutes}}</span>
                                    <span :style="{'color':(status==0?'#F5A623':(status==1?'#17B7BD':'#646464'))}">:</span>
                                    <span
                                            class="item"
                                            :style="{'background':(status==0?'#F5A623':(status==1?'#17B7BD':'#646464'))}"
                                    >{{ timeData.seconds}}</span>
                                </template>
                            </van-count-down>
                            <!-- <detailCountDown v-if="status==0" :timeBox="flashSaleData.time">

                            </detailCountDown> -->
                        </div>
                    </div>
                </div>
                <!-- 已结束的秒杀商品 -->
                <div class="finishedPrice" :style="{'background':'#999999'}" v-if="status==2">
                    <div>
                        <div class="priceTag">￥</div>
                        <div class="salePrice">{{flashSaleData.spuSkPrice}}</div>
                        <div class="originalPrice" v-if="flashSaleData.originalPrice">￥{{flashSaleData.originalPrice}}</div>
                    </div>
                    <div class="seckillTxt">秒杀已结束</div>
                </div>
                <!-- <div class="finishedPrice" :style="{'background':'#000000'}" v-if="status==-1"></div> -->
                <!--商品名称\简介和分享-->
                <div class="goods">
                    <div class="goodsDetailBox">
                        <div>
                            <div class="spuName">{{lists.spuName}}</div>
                            <div class="salePoint">{{lists.spuSalePoint}}</div>
                        </div>
                        <div class="share" v-if="status!=2">
                            <img class="shareImg" :src="icon.share" @click="wechartShare" />
                            <div>分享</div>
                        </div>
                    </div>
                    <div class="goodsAttribute">
                        <div class="goodsAttributeBox" :style="{'border':'0.5px solid #17B7BD'}" v-if="lists.goodsBrand">品牌：{{lists.goodsBrand}}</div>
                        <div class="goodsAttributeBox" :style="{'border':'0.5px solid #17B7BD'}" v-if="lists.prodArea">产地：{{lists.prodArea}}</div>
                    </div>
                </div>

                <!--商品标签.规格.领券.购买数量-->
                <div class="tag" ref="changeTop">
                    <div class="oneTag" v-show="lists.cutOffTimeStr">
                        <div class="redPoint"></div>
                        <div class="tagInfo">{{lists.cutOffTimeStr}}</div>
                    </div>
                    <div class="oneTag" v-show="lists.labelList.length!=0" v-for="(tagInfoItem,index) in lists.labelList">
                        <div class="redPoint"></div>
                        <div class="tagInfo">{{tagInfoItem}}</div>
                    </div>
                </div>
                <div class="spec" >
                    <div class="specTit">规格</div>
                    <div class="specName">{{skuList.attrValueContent}}</div>
                </div>
                <div class="borderLine"></div>
                <!--购买数量-->
                <div class="shoppingNum">
                    <div>购买数量</div>
                    <div class="stepperBox">
                        <img class="subtractBtn" src="../../../assets/images/mall/mall_icon_subtract.png" alt="">
                        <img class="addBtn" src="../../../assets/images/mall/icon_add_cut.png" alt="">
                        <van-stepper  class="stepper" v-model="goodsNum" integer  @overlimit="addNumBtn" @change="getCount"></van-stepper>
                    </div>

                </div>
                <div class="borderBox"></div>
                <!-- 店铺信息 -->
                <div class="shopInfoBox" @click="toShop">
                    <div class="shopInfo">
                        <div><img class="shopPic" :src="shopInfo.shopPicUrl==null?ownerPicUrl:shopInfo.shopPicUrl" /></div>
                        <div>
                            <div class="shopName">{{shopInfo.shopName}}</div>
                            <div class="goodsSaleNum">在售商品{{shopInfo.saleCount}}件</div>
                        </div>
                    </div>
                    <div class="toShop">进店逛逛 &nbsp;<img :src="icon.evaluate"/></div>
                </div>
                <div class="borderBox"></div>
                <!--商品评价-->
                <div class="evaluate" ref="titChange">
                    <div class="evaluateTit">商品评价</div>
                    <div class="evaluateCli" @click="showMoreEval()">
                        <span>查看所有评价</span>
                        <img :src="icon.evaluate" />
                    </div>
                </div>
                <!--显示第一条评价-->
                <div v-if="show.firstEvaluate" class="evaContent">
                    <div :style="{'display': 'flex','align-items': 'center','justify-content': 'space-between'}">
                        <div :style="{'display': 'flex','align-items': 'center'}">
                            <img class="evalOwnerPic" :src="(!evalInfo.ownerPicUrl || evalInfo.ownerPicUrl==null)?ownerPicUrl:evalInfo.ownerPicUrl" />
                            <!--如果用户头像为空则显示默认头像-->
                            <div class="ownerName">{{evalInfo.ownerName}}</div>
                        </div>
                        <van-rate class="evaluateRate" v-model="evalInfo.evalLevel" size='0.37rem' allow-half gutter=3 :icon="icon.like" :void-icon="icon.noLike" void-color="#eee" readonly />
                    </div>
                    <div class="evaTxt">{{evalInfo.evalContent}}</div>
                    <div v-for="(item, index) in evalInfo.evalPicUrl">
                        <img class="evalPic" v-lazy="item.picUrl" @click="preview(evalInfo.evalPicUrl,index)"/>
                    </div>
                </div>
                <!--商品详情-->

                <div class="borderBox" v-if="!lists.detailDescribe" ref="detailChange"></div>
                <div class="describe" v-if="lists.detailDescribe"  ref="detailChange">
                    <div class="describeTxt">-商品详情-</div>
                    <div class="describeDetail" v-html="lists.detailDescribe"></div>
                </div>
                <div ref="placeHolder" style="height:600px" v-show="isShowPlaceHolder"></div>
            </div>
        </div>
        <van-goods-action :style="{paddingBottom:this.$footBarHeight+'px'}">
            <van-goods-action-icon :icon="icon.shop" text="商家" @click="toShop()" />
            <van-goods-action-icon :icon="icon.callShop" text="联系商家" @click="callShop" />
            <van-goods-action-button :class="{addCartBtn:skuList.storeNumber==0}" class="addCartBtn1" type="danger"
                                     text="加入购物车" @click="addToChart()" />
            <van-goods-action-button v-show="status==0&&flashSaleData.time>300000"
                                     class="addCartBtn2"
                                     :style="{'background': '#F5A623'}"
                                     type="danger"
                                     :text=remind
                                     @click="remindSale(flashSaleData.startTime)"
            />
            <!-- 距离开抢5分钟时显示 -->
            <van-goods-action-button
                    v-if="status==0&&flashSaleData.time<=300000"
                    :style="{'opacity': '0.53','background': '#F5A623'}"
                    type="danger"
                    text="即将开始"
                    @click="toBuy()"
            />
            <!-- 秒杀中 -->
            <van-goods-action-button
                    v-if="status==1"
                    :style="{'background-image':'linear-gradient(154deg, #59b1a3 0%, #17B7BD 57%)'}"
                    type="danger"
                    text="立即秒杀"
                    @click="toBuy()"
            />
            <!-- 补货中 -->
            <van-goods-action-button
                    v-if="status==3||status==4"
                    :style="{'background': '#999999'}"
                    type="danger"
                    text="补货中"
                    @click="saleOut"
            />
            <!-- 秒杀结束已下架 -->
            <van-goods-action-button
                    v-if="status==2"
                    :style="{'background': '#999999'}"
                    type="danger"
                    text="已结束"
                    @click="goodsDown"
            />
        </van-goods-action>
        <!--点击分享弹窗-->
        <van-action-sheet v-model="show.weChartShare" cancel-text="取消" @cancel="onCancel" round>
            <p class="shareTit">分享至</p>
            <div :style="{'display':'flex','justify-content':'center'}">
                <div class="shareChart"  @click="checkStoragePermissions(1)">
                    <img :src="icon.shareChart" />
                    <p>微信好友</p>
                </div>
                <div class="shareFriends"  @click="checkStoragePermissions(2)">
                    <img :src="icon.shareFriends" />
                    <p>朋友圈</p>
                </div>
            </div>
            <div class="borderLine"></div>
        </van-action-sheet>
        <div class="topBtn" v-show="isShowTopBtn" @click="goTopBtn">
            <img src="@/assets/images/mall/top.png" alt="">
        </div>
        <!-- 放大预览图片 -->
        <Preview :images="images" ref="myPreview" v-if="isShowPreview"></Preview>
    </div>
</template>

<script>
    import {flashSaleGoods} from '../../../page/product/flashSaleGoods'
    export default {
        name: 'flashSaleGoods',
        mixins:[flashSaleGoods]
    }
</script>

<style scoped="scoped" lang="scss">
    .stepperBox{
        position: relative;
        .subtractBtn{
            width: 22px;
            height: 22px;
            position: absolute;
            top: 12px;
            left: 0;
        }
        .addBtn{
            width: 22px;
            height: 22px;
            position: absolute;
            top: 12px;
            right: 0;
        }
        /deep/.van-stepper__minus{
            opacity: 0;
        }
        /deep/.van-stepper__plus{
            opacity: 0;
        }
    }
    /* 回到顶部按钮样式 */
    .topBtn{
        width: 50px;
        height: 50px;
        position: fixed;
        bottom: 80px;
        right: 20px;
        z-index: 10;
        img{
            width: 100%;
        }
    }
    #goodsPage {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
    }
    .title {
        width: 100%;
        height: 48px;
        position: fixed;
        z-index: 10;
        top: 0;
        /*padding: 0 18px;*/
    }
    .actTitle {
        width: 100%;
        height: 48px;
        position: fixed;
        z-index: 10;
        top: 0px;
        /*padding: 0 18px;*/
        background: #ffffff;
    }
    .mianScroll{
        height: 100%;
        overflow: auto;
        margin-bottom: 55px;
    }
    .swipe {
        background-repeat: no-repeat;
        background-size: 100% 100%;
        object-fit: cover;
        height: 375px!important;
        width: 375px!important;
    }
    .van-swipe img {
        height: 100%;
        width: 100%;
        object-fit: cover;
        /*background-color: #42b983;*/
    }
    .arrow {
        width: 30px;
        padding: 10px 10px 3px 15px;
    }
    .collection {
        width: 30px;
        /*padding:27px 10px 3px 0px;*/
        padding-top: 10px;
        padding-right: 10px;
        float: right;
    }
    .afterCollection{
        width: 30px;
        /*padding:27px 10px 3px 0px;*/
        padding-top: 10px;
        padding-right: 10px;
        float: right;
    }
    .cart {
        width: 30px;
        /*padding: 27px 18px 3px 0px;*/
        float: right;
        padding-top: 10px;
        padding-right: 15px;
    }
    .cart /deep/img {
        display: block;
        /*width: 100%;*/
        height: 30px;
        width: 30px;
    }
    // .van-info {
    //   top: 34px;
    //   right: 15px;
    // }
    .van-info {
        top: 15px;
        right: 15px;
        height: 15px;
        font-size: 8px;
        min-width: 15px;
        line-height: 15px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .tabChange{
        position:absolute;
        top: 15px;
        left: 100px;
        display: flex;
        width: 150px;
        flex-direction: column;
        .tabTxtList{
            width: 100%;
            display: flex;
            justify-content: space-around;
        }
    }
    .noDetailTabChange{
        position:absolute;
        top: 15px;
        left: 140px;
        display: flex;
        width: 100px;
        flex-direction: column;
        .tabTxtList{
            width: 100%;
            display: flex;
            justify-content: space-around;
        }
    }
    .stock {
        font-family: PingFangSC-Regular;
        font-size: 12px;
        color: #9a9a9a;
        letter-spacing: -0.04px;
        line-height: 21px;
        float: right;
    }
    .lowStock {
        float: right;
        font-family: PingFangSC-Regular;
        font-size: 12px;
        color: #17B7BD;
        letter-spacing: -0.04px;
        text-align: right;
        line-height: 21px;
    }
    .originalPrice {
        text-decoration: line-through;
        opacity: 0.8;
        font-family: PingFangSC-Regular;
        font-size: 14px;
        color: #ffffff;
        letter-spacing: -0.04px;
        text-align: left;
        line-height: 15px;
        padding-left: 1px;
        display: block;
        // padding-bottom: 5px;
    }
    .price {
        width: 100%;
        height: 52px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .priceLeftBox{
        margin:7px 15px;
        .priceBox{
            display: flex;
            align-items: flex-end;
            .priceTag {
                font-size: 18px;
                letter-spacing: -0.04px;
                font-family: PingFangSC-Medium;
                color: #ffffff;
                line-height: 18px;
            }
            .salePrice {
                font-family: PingFangSC-Medium;
                font-size: 30px;
                line-height: 28px;
                color: #ffffff;
                letter-spacing: -0.07px;
            }
        }
    }
    .priceRightBox{
        display: flex;
    }
    .repertoryBox{
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-right: 15px;
        /*justify-content: center;*/
        div:first-child{
            font-family: PingFangSC-Regular;
            font-size: 14px;
            color: #FFFFFF;
            line-height: 19px;
            letter-spacing: 0;
            text-align: center;
            margin-bottom: 7px;
            margin-top:5px;
        }
        div:last-child{
            min-width: 53px;
            height: 16px;
            background: #fff;
            font-family: PingFangSC-Regular;
            font-size: 14px;
            color: #17B7BD;
            border-radius: 50px;
            letter-spacing: 0;
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 0 6px;
        }
    }
    .timeRightBox{
        width: 114px;
        height: 52px;
        /*margin-right: 10px;*/
        background: rgb(255,236,237);
        /*display: flex;*/
        /*align-items: baseline;*/
    }
    .finishedPrice{
        height: 52px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-left:15px;
        // .seckillTxt{
        //   margin-right: 15px;
        // }
    }

    .seckillTxt {
        font-family: PingFangSC-Regular;
        font-size: 14px;
        color: #17B7BD;
        text-align: center;
        letter-spacing: -0.04px;
        /*margin-right: 13px;*/
    }
    .seckillDate {
        font-family: PingFangSC-Medium;
        font-size: 17px;
        color: #ffffff;
        letter-spacing: -0.04px;
    }
    /deep/.van-count-down{
        margin-left:3px;
    }
    .item {
        display: inline-block;
        width: 20px;
        height: 20px;
        margin: 0 6px !important;
        color: #fff;
        font-size: 12px;
        text-align: center;
        line-height: 20px;
        border-radius: 2px;
    }
    .spuName {
        width: 313px;
        font-family: PingFangSC-Medium;
        font-size: 17px;
        color: #333333;
        letter-spacing: -0.04px;
        padding-bottom: 2px;
        display: -webkit-box;overflow: hidden;text-overflow: ellipsis;-webkit-line-clamp: 2;-webkit-box-orient: vertical;
    }
    .salePoint {
        width: 313px;
        font-family: PingFangSC-Regular;
        font-size: 15px;
        color: #9a9a9a;
        letter-spacing: -0.04px;
        line-height: 21px;
        display: -webkit-box;overflow: hidden;text-overflow: ellipsis;-webkit-line-clamp: 2;-webkit-box-orient: vertical;
    }
    .share {
        height: 45px;
        font-family: PingFangSC-Regular;
        font-size: 10px;
        color: #9a9a9a;
        letter-spacing: -0.03px;
        text-align: right;
        line-height: 21px;
        border-left: 0.5px solid #E5E5E5;
    }
    .shareImg {
        width: 20px;
        height: 20px;
        display: block;
        padding-left: 15px;
        padding-top:8px;
    }
    .goods{
        padding: 10px 15px;
        .goodsDetailBox{
            display: flex;
            justify-content: space-between;
            width: 100%;
        }
    }
    .goodsAttribute{
        display: flex;
        flex-wrap: wrap;
        color: #17B7BD;
        font-size: 12px;
        // margin-top:6px;
        .goodsAttributeBox{
            border: 0.5px solid #17B7BD;
            border-radius: 10px;
            padding: 2px 7px;
            margin-right: 5px;
            margin-top: 5px;
            max-width: 85%;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
        }
    }
    .tag{
        font-family: PingFangSC-Regular;
        font-size: 10px;
        width: 92%;
        color: #333333;
        letter-spacing: -0.04px;
        padding: 5px 15px;
        background: #F9F9F9;
        line-height: 28px;
        display: flex;
        display: -webkit-flex;
        /* align-items: center; */
        flex-wrap: wrap;
        flex-direction:column;
    }
    .oneTag{
        display: flex;
        /* margin-right: 30px; */
    }

    .redPoint {
        width: 4PX;
        height: 4PX;
        background-color: #ff5050;
        border-radius: 50% !important;
        margin-right: 5px;
        margin-top: 12px;
    }
